<template>
  <div >


  <n-space vertical>

<n-layout >
  <n-layout-header :inverted="inverted" bordered style="height: 60px">
    <n-space style="flex-direction: row-reverse;margin-right: 30px" >
      <n-dropdown @select="handleSelect" trigger="hover" :options="options">

        <n-avatar
          :size="48"
          :src="img"
      />

      </n-dropdown>
    </n-space>

  </n-layout-header>
    <n-layout has-sider>
      <n-layout-sider
          bordered
          collapse-mode="width"
          :collapsed-width="64"
          :width="240"

          show-trigger
          :inverted="inverted"
      >

        <n-menu router
                @update:value="handleUpdateValue"
                :inverted="inverted"
                :collapsed-width="64"
                :collapsed-icon-size="22"
                :options="menuData"

                :render-icon="renderMenuIcon"


        style="height: 630px"></n-menu>

      </n-layout-sider>
      <n-layout style="max-height: 630px;margin-top: 30px;margin-left: 40px;margin-right: 40px">
        <n-breadcrumb>
          <n-breadcrumb-item>
            <n-icon> <HomeOutline/> </n-icon> <router-link to="/home">首页</router-link>
          </n-breadcrumb-item>

          <n-breadcrumb-item><n-icon><BugOutline/></n-icon>
            {{$router.currentRoute.value.name}}

          </n-breadcrumb-item>
        </n-breadcrumb>
        <div v-if="router.currentRoute.value.path==='/home'">
          <HomeView/>
        </div>
        <router-view/>

      </n-layout>

    </n-layout>

</n-layout>
  </n-space>
  </div>

</template>

<script>
import {h, defineComponent, ref, computed, onMounted, reactive, render} from 'vue'
import { NIcon,useMessage,NText,NMenu,useDialog,useNotification} from 'naive-ui'
import {useStore,createStore}from'vuex'
import {useRoute,useRouter} from 'vue-router'
import {BookOutline,HomeOutline,BugOutline,KeyOutline,PeopleOutline,DocumentAttachOutline,ChatboxOutline,SearchCircleOutline,HeadsetOutline,ListCircleOutline} from '@vicons/ionicons5'
import {postRequest,getRequest} from '../api/request'
import {NaturePeopleOutlined,PeopleOutlineFilled,RealEstateAgentOutlined,SupportAgentFilled,SettingsOutlined
  ,PlaylistAddCheckRound,FeaturedPlayListOutlined} from '@vicons/material'

import HomeView from '../views/HomeView'





export default ({
  components:{
    HomeOutline,
    BugOutline,
    HomeView
  },
  setup () {
const message=useMessage();
    const store=useStore();
    const router=useRouter();
    const route=useRoute();
    const peopleMessage=ref([])
    const img=ref()
    const dialog=useDialog()
    const notification=useNotification()
    const userface=ref("https://xl-1258185807.cos.ap-guangzhou.myqcloud.com/xlmall/images/2021/07/04/ae81e37b-ca45-438c-aab7-9d70fd2e2807.png")
    onMounted(()=>{


    })
    const menuData=computed(()=>{
      return store.state.routes

    })
    console.log(menuData.value)
    //获取本地session里面的user用户信息
    peopleMessage.value=window.sessionStorage.getItem("user")
    img.value=window.sessionStorage.getItem("userface");
    // userface.value=window.sessionStorage.getItem("user").data.msg.userface
    console.log(img.value)



    return {
      home:ref(true),
      img,
      userface,
      renderMenuLabel (option) {
        return h(
            NIcon,{

            },
            {
              default:()=>h(BookOutline)
            }
        )
      },
      handleUpdateValue (key, item) {
        router.push(item.path)

      },
      inverted: ref(false),
      router,
      menuData,
      peopleMessage,
      options: [
        {
          label: '个人信息',
          key: '/peoMessage',
          disabled:false
        },
        {
          label: '退出',
          key: '/logout',
          disabled:false,

        },],
      handleSelect(key){
        if(key==="/peoMessage"){
          router.push(key)
        }else{
          dialog.warning({
            title:'警告',
            content:'你确定注销登录?',
            positiveText: '确定',
            negativeText: '不确定',
            onPositiveClick: () => {
              getRequest(key).then(resp=>{
                if(resp){
                  window.sessionStorage.removeItem("user")
                  window.sessionStorage.removeItem("userface")
                  router.push("/")
                  notification.success({
                    content: '提示',
                    meta: '已经成功注销！',
                    duration: 5000
                  })
                }
              })
            },
            onNegativeClick: () => {
              message.error('不确定')
            }

          })



        }

      },


      // menus渲染图标
      renderMenuIcon (option) {
        if (option.iconCls === 'BookOutline') {
          return h(NIcon, null, {default: () => h(BookOutline)})
        } else if (option.iconCls === 'KeyOutline') {
          return h(NIcon, null, {default: () => h(KeyOutline)})
        }else if(option.iconCls==='NaturePeopleOutlined'){
          return h(NIcon, null, {default: () => h(NaturePeopleOutlined)})
        }else if(option.iconCls==='PeopleOutlineFilled'){
          return h(NIcon, null, {default: () => h( PeopleOutlineFilled)})

        }else if (option.iconCls==='PeopleOutline'){
          return h(NIcon, null, {default: () => h( PeopleOutline)})
        }else if (option.iconCls==='RealEstateAgentOutlined'){
          return h(NIcon, null, {default: () => h( RealEstateAgentOutlined)})
        }else if (option.iconCls==='SupportAgentFilled'){
          return h(NIcon, null, {default: () => h( SupportAgentFilled)})
        }else if (option.iconCls==='SettingsOutlined'){
          return h(NIcon, null, {default: () => h( SettingsOutlined)})
        }else if (option.iconCls==='PlaylistAddCheckRound'){
          return h(NIcon, null, {default: () => h( PlaylistAddCheckRound)})
        }else if(option.iconCls==='FeaturedPlayListOutlined'){
          return h(NIcon, null, {default: () => h( FeaturedPlayListOutlined)})
        }else if(option.iconCls==='DocumentAttachOutline'){
          return h(NIcon, null, {default: () => h(DocumentAttachOutline)})
        }else if(option.iconCls==='ChatboxOutline'){
          return h(NIcon, null, {default: () => h(ChatboxOutline)})
        }
      else if(option.iconCls==='SearchCircleOutline'){
          return h(NIcon, null, {default: () => h(SearchCircleOutline)})
        }
      else if(option.iconCls==='HeadsetOutline'){
          return h(NIcon, null, {default: () => h(HeadsetOutline)})
        }
      else if(option.iconCls==='ListCircleOutline'){
          return h(NIcon, null, {default: () => h(ListCircleOutline)})
        }



      }}
  }

})
</script>
