import { createStore } from 'vuex'

export default createStore({
  state: {
    routes:[],
    sessions:{},

    currentSession: null,
    currentPeople: JSON.parse(window.sessionStorage.getItem("user")),
    filterKey: '',
    stomp: null,
    isDot: {},
    user: JSON.parse(window.localStorage.getItem('loginUser'))
  },
  mutations: {
    INIT_CURRENTPEOPLE(state,people){
      state.currentPeople=people;
    },
    initRoutes(state,data){
      state.routes=data;
    },
    INIT_USER(state,data){

      state.user=data;
      window.localStorage.setItem('loginUser', JSON.stringify(data));


    }
  },
  actions: {
  },
  modules: {
  }
})
