<template>
  <div class="login-wrap">

    <div class="ms-login">
      <div class="ms-title">包有房管理系统</div>
      <n-form :model="param" :rules="rules" ref="login"  size="small" class="form">
        <n-form-item-row  path="username" label="账号">
          <n-input  placeholder="username" v-model:value="param.username"></n-input>
        </n-form-item-row>
        <n-form-item-row  path="password" label="密码">
          <n-input
              @keyup.enter.native="submitForm()"
              show-password-toggle
              type="password"
              placeholder="password"  v-model:value="param.password"></n-input>
        </n-form-item-row>

          <n-button  size="small" block type="info"  attr-type="button" @click="submitForm()"><span style="font-weight: 600;font-size: 13px">登录</span></n-button>

      </n-form>
      <div style="padding: 0 38px"><n-divider title-placement="center"><span style="color: #849aae;font-size: 10px">或</span></n-divider></div>
      <div style="padding: 0 38px;display: flex;align-items: center;">
        <n-button @click="showTips" text style="margin-left: 20px"> <n-icon size="22" color="#385185">
        <logo-wechat />
      </n-icon>
        </n-button>
        <n-button  @click="showTips" text style="margin-left: 20px">
        <n-icon size="22" color="#385185">
          <logo-facebook />
        </n-icon></n-button>
        <span style="color:#385185;font-weight:600;margin:0 auto;text-align: center;">使用您的社交账号登录!</span>
      </div>
      <div style="color:#00376b;font-size:12px;padding: 0 38px;margin: 10px auto;text-align: center">忘记密码了?</div>
    </div>


  </div>
</template>

<script>
import {ref, reactive} from 'vue'
import {useRoute,useRouter} from 'vue-router'
import {postRequest} from "../api/request";
import {useStore,createStore} from 'vuex'
import {LogoWechat,LogoFacebook} from "@vicons/ionicons5"
import {useMessage} from "naive-ui"


export default {
  name: "Login",
  components:{
    LogoWechat,
    LogoFacebook
  },

  setup(){
    //路由
    const route=useRoute();
    const router=useRouter();
    const message=useMessage()
    //store
    const store=useStore();
    // const createStore=createStore();
    const param=reactive({

      username:'admin',
      password:''
    })
    const login=ref(null)

    const submitForm=()=>{
      login.value.validate((errors)=>{
        if(!errors){
          postRequest('/login',param).then(resp=>{
            if(resp.data.status==="error"){
              message.error(resp.data.msg)
            }else if(resp.data.status==="success"){
              store.commit('INIT_CURRENTPEOPLE', resp.obj);
              window.sessionStorage.setItem("user", JSON.stringify(resp.data.msg))
              window.sessionStorage.setItem("userface",resp.data.msg.userface)
              let path=route.query.redirect;
              router.replace((path == '/' || path == undefined) ? '/home' : path)
            }

          })
        }
      })
    }

    return{
      login,
      param,
      submitForm,
      //自定义验证规则
      rules:{
        username:[{required:true,message:'请输入账号',trigger:'blur'}],
        password:[{required:true,message:'请输入密码',trigger:'blur'}]
      },
      showTips:()=>{
        message.info('暂未开放！')
      }


    }



  }

}
</script>

<style scoped>

.login-wrap {
/*  background: url("../assets/bg.jpg") ;*/
/*  background-size: 100% 100%;*/

  width: 100%;

/*;*/

}
.ms-title {
  width: 100%;
  line-height: 50px;
  text-align: center;
  font-size: 17px;
  font-weight: 600;
  color: #00376b;
  /*color: #fff;*/
  border-bottom: white;
}
.ms-login {
  width: 350px;
  height: 378px;
  border: 1px solid gainsboro;
  margin: 70px auto;
  align-items: center;
}
.ms-content {
  padding: 30px 30px;
}

.login-btn button {
  width: 100%;
  height: 36px;
  margin-bottom: 10px;
}
.login-tips {
  font-size: 12px;
  line-height: 30px;
  color: #fff;
}
.form{
  padding: 0px 38px;

}
</style>
